import { __decorate } from "tslib";
import { Component, Vue, Watch } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import commodityAdd from './commodity-add.vue';
import pageView from '@/components/common-ui/page-view.vue';
import { EventBus } from '@/until/event-bus';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.classId = '';
        this.className = '';
        this.shopId = '';
        this.commodityList = [];
        this.commodityObj = {
            name: '',
            price: 0,
            unit: '',
            disabled: false,
            number: 0,
            imgUrlList: []
        };
    }
    mounted() {
        let queryObj = this.$route.query;
        this.classId = queryObj.id;
        this.shopId = queryObj.shopId;
        this.className = queryObj.name;
        EventBus.$on('commodityManage', (msg) => {
            this.saveSuccess(msg.type, msg.data);
        });
    }
    deactivated() {
        this.classId = '';
        this.$set(this, 'commodityList', []);
    }
    idChange(newId) {
        if (newId) {
            this.getCommodityListByClassId();
        }
    }
    getCommodityListByClassId() {
        if (!this.classId) {
            return;
        }
        const toast = this['$createToast']({
            time: 0,
            txt: '商品获取中...'
        });
        toast.show();
        this.$store.dispatch('getCommodityListByClassId', { class: this.classId, page: 1 }).then((response) => {
            if (response.success) {
                let list = response.data.list;
                this.$set(this, 'commodityList', list);
                toast.hide();
            }
        });
    }
    addCommodity() {
        let params = {
            data: {
                name: '',
                price: 0,
                unit: '',
                disabled: false,
                number: 0,
                imgUrlList: []
            },
            classId: this.classId,
            shopId: this.shopId
        };
        this.$router.push({
            name: 'commodityAdd',
            params: params
        });
    }
    saveSuccess(type, data) {
        if (type === 'add') {
            this.commodityList.push(data);
        }
        else {
            let index;
            this.commodityList.some((item, index) => {
                if (data.id === item.id) {
                    index = index;
                    return true;
                }
            });
            this.$set(this.commodityList, index, data);
        }
    }
    editCommodity(item) {
        let params = {
            data: item,
            classId: this.classId,
            shopId: this.shopId
        };
        this.$router.push({
            name: 'commodityAdd',
            params: params
        });
    }
};
__decorate([
    Watch('classId')
], AppList.prototype, "idChange", null);
AppList = __decorate([
    Component({
        components: {
            page,
            commodityAdd,
            pageView
        }
    })
], AppList);
export default AppList;
